
html,body, #root {
  /* background-color: rgb(23, 42, 59); */
  height: 100%; 
  /* background-color: rgb(10, 17, 23);  */
  background-color: rgb(7, 14, 20); 
}

@font-face {
  font-family: 'OPTIBankGothic-Light';
  src: local('OPTIBankGothic-Light'),
  url('./OPTIBankGothic-Light.otf') format('truetype');
}



#main-container{
  display: flex;
  /* border: 3px solid yellow;   */
  flex-direction: row;
  height: 100%;
}

 


#picture{ 
  width: 152vh;
  margin:0;  
  height: 100vh;
  background-image: url("Olga2.jpg");
  background-position: cover;  
  background-repeat: no-repeat; 
  background-size: contain;
  /* border: 3px solid rgb(227, 28, 28);   */
}


#menu-box {
 text-align: left; 
 display: flex;
 flex-direction:column;
 justify-content: center;
 /* border: 1px solid blue; */
 color: rgb(247 200 34);
}

#info-box {
height: 70%;
margin-top: 20%;
/* border: 1px solid blue;     */
justify-content: flex-end;
}


 #main-container-mob{
  height: 100%;
  width: 100%;
} 

#mobile-header{
  position: relative;
  background-image: url("OlgaMob-header.jpg");
  background-position: cover;  
  background-repeat: no-repeat; 
  background-size: contain; 
  width: auto;
  height: 15%;
  /* border: 1px solid red;   */
}

#menubtn{
  position: absolute;
  right: 15px;
  bottom: 10px;
  width: 20px;
  height: 15px;
  cursor: pointer; 
  /* border: 1px solid red;  */
}

#mobile-body {
  display: flex;
  background-image: url("OlgaMobilePicLong.jpg");
  background-position: cover;  
  background-repeat: no-repeat; 
  background-size: contain;
  height: 85%;
}

#info-box-mob{
  /* border: 2px solid green;  */
  height: 100%;
  width: 72%;
  display: flex;
}

#dropdown {
  /* float:right; */
  width: 28%;
  height: 100%;
  opacity: 0.6;
  /* border: 2px solid red;     */
}
#dropdown-content {
  display: none;
  padding-top: 6px;
  background-color:rgb(7, 14, 20); 
  height: 100%;
  /* border: 2px solid red;   */
}  

#dropdown-content p {
  color: rgb(247 200 34);
  padding: 5px 10px;
  display: block; 
  margin: 10px auto;
  font-family: OPTIBankGothic-Light;
  font-size: 9px;
  letter-spacing: 2px;
  font-weight: bold;
}

#dropdown-content p:hover {
  background-color: rgb(51, 77, 100);
}

#menu-box > p {
  padding: 2%;
  margin: 4%;
  /* border: 1px solid red;    */
  margin-right: 70%;
  font-family: OPTIBankGothic-Light;
  font-size: 9px;
  letter-spacing: 2px;
}

#component {
/* border: 1px solid red;  */
height: 100%;
background-color: rgb(7, 14, 20);
opacity: 0.6;
width:fit-content;
float:right;
padding: 2%;
color: rgb(247 200 34);
display: flex;
flex-direction: column;
}

@media only screen and (max-width: 600px) {
  #component { 
    height: 100%;
    width: 100%;
    opacity: 0.9;
    color: rgb(247 200 34); 
    background-color: rgb(7, 14, 20);
    /* border: 3px solid red;   */
    align-self: center;
    padding: 0;
  }
} 

#component-gallery {
  height: 100%;
  background-color: rgb(7, 14, 20);
  opacity: 1;
  padding: 0;
  color: rgb(247 200 34);    
}

#component > p{
  font-family: 'Segoe UI';
  font-size: 8pt;
  /* line-height: 10pt; */
}

#component > h4{
  font-family: OPTIBankGothic-Light;
  font-size: 9px;
  letter-spacing: 2px;
}

#five-pics {
  width:90%;
  margin-left: 5%;  
}

@media only screen and (max-width: 600px) {
  #five-pics {
    width: 100%;
    margin: 0;
  }
}

#resume {
  width: 445px;
  height: auto;
}

@media only screen and (max-width: 600px) {
  #resume {
    width: 100%;
    height: auto;
  }
}


